@import url(https://fonts.googleapis.com/css?family=Gayathri&display=swap);
body {
  margin: 0;
  font-family: 'Gayathri', sans-serif;
}

.header {
  background-color: #282c34;
  text-align: center;
  height: auto;
  display: flex;
  padding: 20px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(5px + 2vmin);
  color: white;
}

.header h2 {
  font-size: 30px;
}

.title {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.github-logo {
  display: block;
  background: url(/static/media/github-logo.53939ac9.svg);
  width: 70px;
  height: 70px;
  margin: 0 30px;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 80vh;
}

.searchField input[ type = "text" ] {
  font-size: 20px;
  box-shadow: rgba( 200, 200, 200, .1 ) 0 0 10px 2px;
  border: none;
  outline: none;
  padding: 5px;
  color: #535353;
  border: 4px solid white;
  border-radius: 5px;
}

.card {
    display: flex;
    flex-wrap: wrap;
    padding: 20px;
    box-shadow: rgba( 40, 44, 52, 0.3 ) 0 0 12px 2px;
    margin: 20px;
    border-radius: 20px;
    width: 900px;
}

.card-img {
    width: 200px;
    height: 170px;
    margin: 0 10px;
}

.card-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 10px 15px;
}

.card-description {
    max-width: 600px;
}

.card-content div {
    padding: 5px 0;
}

.card-title {
    font-size: 30px;
    font-weight: 600;
}

.card-extras-info {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.card-extras-info div {
    padding: 0 5px;
    margin: 10px 0;
}

.card-extras-info .common {
    box-shadow: rgba( 40, 44, 52, 0.3 ) 0 0 21px 2px;
    text-align: center;
    color: white;
    font-weight: 600;
    margin-right: 10px;
    padding: 10px 5px 5px 5px;
    width: 100px;
    border-radius: 10px;
}

.card-description a {
    text-decoration: none;
    font-weight: 600;
    color: #0f6fec;
}

.card-description a:hover {
    color: black;
}

.stars {
    background-color: #0072dd;
}

.issues {
    background-color: #c40000;
}

@media only screen and (max-width: 960px) {
    .card {
        width: 600px;
    }
}

@media only screen and (max-width: 679px) {
    .card {
        width: 220px;
    }
}

